import React, { useEffect, useRef } from 'react'
import { FixedSizeList as List } from 'react-window';

import Box from '../../components/Box'
import Text from '../../components/Text'

const Row = ({ index, data, style }) => {
  const n = 105 - index
  return (
    <div style={style}>
      {n >= 0 && n <= 100 && (
        <Box.Inline color="white" borderBottom={data === index && '4px solid white'}>
          <Text fontSize="5em">{n}</Text>
        </Box.Inline>
      )}
    </div>
  )
};

const Countdown = ({ progress, height, width }) => {
  const listRef = useRef();
  useEffect(() => {
    listRef.current.scrollToItem(progress + 10);
  }, [progress])
  return (
    <List
      height={height}
      itemData={progress + 5}
      itemCount={101 + 10}
      itemSize={124}
      width={width}
      ref={listRef}
    >
      {Row}
    </List>
  )
}

export default Countdown
