window.gaTrackClick = (label, part = 0, page = 0, slide = 0) => {
  const mark = [part, page, slide].filter(Boolean).join('-');
  console.log(KEY + label + (mark ? `-${mark}` : ''))

  if (window.gtag) {
    window.gtag('event', 'click', {
      'event_category': 'button',
      'event_label': KEY + label + (mark ? `-${mark}` : ''),
    });
  }
}

const ensureTrailingSlash = url => {
  const end = url.slice(-1);
  return end === '/' ? url : `${url}/`
}

window.gaTrackPageView = (part, page, slide = 1) => {
  console.log(`${ensureTrailingSlash(window.location.pathname)}#/${part}-${page}-${slide}`)
  if (window.gtag) {
    window.gtag('event', 'page_view', {
      page_path: `${ensureTrailingSlash(window.location.pathname)}#/${part}-${page}-${slide}`,
    })
  }
}
